h1,h2,h3,h4{
    padding:0;
    margin:0;
}
.container{
    width:300px;
    padding:100px;
    background-color:#eee;
    border-radius:5px;

}
.button{
    background-color:#3498db;
    display:block;
    padding:10px 15px;
    margin:10px 0px;
    color:white;
    cursor: pointer;
    text-align: center;
}
input{
    margin:10px 0px;
    width:100%;
    box-sizing:border-box;
    padding:8px;
    border:0;
}
html,body{
    height:100%;
}
body{
    display:flex;
    /* align-items: center; */
    justify-content: center;
    font-family: sans-serif;
    padding-top:100px;
}
.commonsize{
    width:90vw;
    padding:20px;
    box-sizing: border-box;
}
.urlentry{
    background-color:#2980b9;
    color:white;
    box-shadow:1px 1px 4px 0px #ccc;
}
.success{
    background-color:#27ae60;
    color:white;
    margin-top:20px;
    border-radius:10px;
}
.url-list{
    width:90vw;
}
.url{
    padding:20px;
    background-color:#e67e22;
    margin-bottom:3px;
}
a{
    color:#fff;
}
.shortened-url{
    font-size:1.4em;
    color:#fff;
}
.smalltext{
    margin-top:10px;
}
.smalltext a{
    font-size:0.8em;
    color:#eee;
    text-decoration: none;
}
.empty{
    text-align:center;
    background:#eee;
    padding:40px;
    font-size:1.8em;
}